@charset "UTF-8";
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif; }

html {
  width: 100%;
  height: 100%; }

section {
  width: 100%; }

#app {
  width: 100%;
  height: 100%; }

.brand-list-item {
  position: relative;
  width: 100%;
  height: 230px;
  background-color: #cccccc;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
  .brand-list-item .brand-title {
    position: absolute;
    right: 20px;
    bottom: 20px;
    color: #cccccc;
    opacity: 0.8;
    font-size: 30px;
    font-weight: bolder; }
  .brand-list-item .dk {
    color: #C5A4A8; }

.brand {
  height: 100%;
  background-color: #f2f2f2;
  position: relative; }
  .brand .brand-carousel {
    height: 100%; }
    .brand .brand-carousel .swiper-container {
      height: 100%; }
      .brand .brand-carousel .swiper-container .swiper-wrapper {
        height: 100%; }
        .brand .brand-carousel .swiper-container .swiper-wrapper .swiper-slide {
          background-color: #cccccc; }
          .brand .brand-carousel .swiper-container .swiper-wrapper .swiper-slide .img-holder {
            width: 100%;
            height: 100%;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover; }
  .brand .nav {
    position: fixed;
    bottom: 13px;
    left: 0;
    width: 100%;
    height: 70px;
    z-index: 999; }
    .brand .nav .btn {
      display: inline-block;
      position: relative;
      width: 50%;
      height: 100%;
      float: left;
      padding-top: 13px;
      background-color: #686868; }
      .brand .nav .btn img {
        display: block;
        width: 30px;
        height: 30px;
        margin: 0 auto;
        vertical-align: middle; }
      .brand .nav .btn .text {
        display: block;
        color: #fff;
        text-align: center;
        vertical-align: middle;
        font-size: 13px;
        line-height: 38px; }
    .brand .nav .active {
      position: relative;
      background-color: #C1C1C1 !important; }
      .brand .nav .active:after {
        content: "▼";
        position: absolute;
        bottom: -20px;
        left: 44%;
        color: #C1C1C1;
        font-size: 23px; }
  .brand .content {
    width: 100%;
    background-color: #f2f2f2; }
    .brand .content .intro {
      display: none;
      padding: 10px;
      padding-top: 20px;
      color: grey;
      font-weight: 200; }
    .brand .content .locations {
      display: none;
      width: 100%;
      overflow-x: hidden;
      overflow-y: scroll; }
      .brand .content .locations .addr-wrap {
        width: 100%;
        min-height: 100px;
        background-color: #f2f2f2; }
        .brand .content .locations .addr-wrap .addr {
          width: 100%;
          padding: 15px 10px 2px 10px;
          background-color: #f2f2f2; }
          .brand .content .locations .addr-wrap .addr p {
            vertical-align: middle;
            font-size: 12px;
            line-height: 10px; }
            .brand .content .locations .addr-wrap .addr p img {
              padding-right: 5px;
              vertical-align: middle; }
        .brand .content .locations .addr-wrap .b-info {
          width: 100%;
          min-height: 30px;
          color: #fff;
          background-color: #dddbdb; }
          .brand .content .locations .addr-wrap .b-info ul {
            display: flex;
            margin: 0;
            padding: 0;
            padding-right: 10px;
            align-items: center;
            flex-direction: row-reverse;
            justify-content: flex-start; }
            .brand .content .locations .addr-wrap .b-info ul li {
              display: inline-block;
              width: 25px;
              height: 25px;
              margin-top: 2px;
              margin-left: 8px;
              border-radius: 50% 50%;
              background-color: #999595;
              background-repeat: no-repeat;
              background-position: center;
              background-size: 70% 70%; }
              .brand .content .locations .addr-wrap .b-info ul li .hidden-text {
                display: none; }

.swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 95px;
  left: 0;
  width: 100%; }

.swiper-pagination-bullet-active {
  opacity: 1;
  background: #686868; }

.call-wrap {
  height: 250px;
  display: flex;
  justify-content: space-around;
  align-items: center; }
  .call-wrap .call img {
    width: 40px;
    display: block;
    margin: 0 auto;
    padding-bottom: 15px; }
  .call-wrap .call span {
    text-align: center; }
  .call-wrap .add img {
    width: 40px;
    display: block;
    margin: 0 auto;
    padding-bottom: 15px; }
  .call-wrap .add span {
    text-align: center; }

.popup {
  z-index: 9999;
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  justify-content: center;
  align-items: center; }
  .popup .content {
    min-height: 250px;
    background-color: rgba(0, 0, 0, 0.9);
    width: 90%;
    border-radius: 5px 5px;
    color: #fff;
    padding: 15px; }

.brand-list-bg {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/bg_brand_list.jpg"); }
  .brand-list-bg .bg-wrap {
    height: 100%;
    width: 100%;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.8);
    padding-top: 20px; }
    .brand-list-bg .bg-wrap h3 {
      text-align: center;
      color: #fff; }

.brand-list-box {
  width: 85%;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap; }
  .brand-list-box .brand-wrap {
    width: 85px;
    height: 110px;
    margin: 10px;
    box-sizing: border-box; }
    .brand-list-box .brand-wrap .brand-list-item {
      width: 85px;
      height: 85px;
      border-radius: 50% 50%; }
    .brand-list-box .brand-wrap .brand-title {
      padding-top: 10px;
      display: block;
      color: #fff;
      text-align: center;
      font-size: 12px; }
